import alt from '../alt-application';
import CameraSerachFormActions from '../actions/CameraSearchFormActions'
import GMapStore from '../stores/GMapStore';

class CameraSearchFormStore{
  constructor(){
    this.bindActions(CameraSerachFormActions)
    this.results = []
  }

  onUpdateResults(cams){
    this.results = cams

    var allCams = GMapStore.getState().cams;
    var markers = GMapStore.getState().markers;
    var visibleIndex = []

    for (var i=0; i<allCams.length; i++){
      for (var j=0; j<cams.length; j++){
    
        if (cams[j].live_id == allCams[i].live_id)
          visibleIndex.push(i)
          // markers[i].setMap(null)
      }
    }

    for (var i=0; i<allCams.length; i++){
      if (!visibleIndex.includes(i))
        markers[i].setVisible(false)
      else
        markers[i].setVisible(true)

    }
  }

  onResetResults(){
    this.results = []
  }

}

export default alt.createStore(CameraSearchFormStore);