import React from 'react';
import GMapStore from '../stores/GMapStore';
import GMapActions from '../actions/GMapActions';

class GMap extends React.Component{
  constructor(props){
    super(props);
    this.state = GMapStore.getState();
    this.onChange = this.onChange.bind(this);
    this.t=this.t.bind(this);
  }

  onChange(){
    this.setState(GMapStore.getState())
  }

  componentDidMount(){
    GMapStore.listen(this.onChange);
    var mapElement = document.getElementById('map');
    var map = new google.maps.Map(mapElement, {
      center: {lat: 40.794628, lng: -73.954743},
      zoom: 12
    });

    GMapActions.setupMarkers(map);
    this.state.gMap = map
    // this.t().then((dd)=>{console.log(dd)})
  }

  //REMOVE ALL MARKERS
  // t(){
  //   this.state.markers.map(m=>{
  //     console.log(m.id)
  //     m.setMap(null)
  //   })
  // }

  // t(){
  //   var addr = "330 E 65th st. new york ny 10065";
  //   var geocoder = new google.maps.Geocoder();
  //   geocoder.geocode({address: addr}, (result, status)=>{
  //     if (status == google.maps.GeocoderStatus.OK){
  //       console.log(result[0].geometry.location.lat())
  //       console.log(result)
  //     }
  //   })
  // }

  t(){
    return new Promise((resolve, reject)=>{
      var addr = "330 E 65th st. new york ny 10065";
      var geocoder = new google.maps.Geocoder();
      geocoder.geocode({address:addr}, (result, status)=>{
        if (status == google.maps.GeocoderStatus.OK){
          resolve({lat: result[0].geometry.location.lat(), lon:result[0].geometry.location.lng()})
        }
        else
          reject(-1);
      })
    })

  }



  componentWillUnmount(){
    GMapStore.unlisten(this.onChange);
  }


  render(){
    return (<div id="map" className="map"></div>)
  }
}

export default GMap;