import React from 'react'
import MapResultTableRow from './MapResultTableRow'
import CameraSearchFormActions from '../actions/CameraSearchFormActions'
import CameraSearchFormStore from '../stores/CameraSearchFormStore'
import GMapActions from '../actions/GMapActions'

class CameraSearchForm extends React.Component{
  constructor(props){
    super(props);
    this.state = CameraSearchFormStore.getState()
    this.onChange = this.onChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this);
    this.geocode = this.geocode.bind(this);
    this.handleReset = this.handleReset.bind(this);
  }

  onChange(){
    this.setState(CameraSearchFormStore.getState())
  }

  componentDidMount(){
    CameraSearchFormStore.listen(this.onChange)
  }

  componentWillUnmount(){
    CameraSearchFormStore.unlisten(this.onChange);
  }

  handleSubmit(){
    let address = this.refs.address.value;

    // Check if input is latlng or address
    let re = new RegExp("^[\-0-9.]+[ ]+[\-0-9.]+$", "g");
    var latlng;
    
    //Case address
    if (re.exec(address) == null){
      this.geocode(address).then((data)=>{
        latlng = data
        CameraSearchFormActions.getNN(latlng)
        // GMapActions.showNNMarkersOnly()
      });
    }
    
    //Case Latlng
    else{
      latlng = {lat:address.split(" ")[0].trim(), lng: address.split(" ")[1].trim()}
      CameraSearchFormActions.getNN(latlng)
    }


    // GMapActions.showNNMarkersOnly()
    
    
  }

  geocode(addr){
    return new Promise((resolve, reject)=>{
      var geocoder = new google.maps.Geocoder();
      geocoder.geocode({address:addr}, (result, status)=>{
        if (status == google.maps.GeocoderStatus.OK){
          resolve({lat: result[0].geometry.location.lat(), lng:result[0].geometry.location.lng()})
        }
        else
          reject(-1);
      })
    })
  }

  handleReset(){
    this.refs.address.value = null
    GMapActions.showAllCams()
    CameraSearchFormActions.clearResults()
  }

  render(){
    var resultsJSX = this.state.results.map((r)=>{
      return <MapResultTableRow live_id={r.live_id} distance={r.distance} location={r.location} key={r.live_id} />
    })
    return(
      <div>
        <div class="row">
          <h4> Find Cameras Near:</h4>
        </div>
        
        <div className="row">
          <input ref="address" className="form-control" type="text" placeholder="lat lng or address" />
        </div>

        <div className="row">
        <button className="btn btn-warning" style={{width: 48+"%", float:"left"}}onClick={this.handleReset}>Reset</button>
          <button className="btn btn-primary" style={{width: 48+"%", float: "right"}}onClick={this.handleSubmit}>Submit</button>
        </div>
        <div className="row">
          <div className="camsTable">
            <table className="table table-stripped">
              <thead>
                <tr>
                  <th>Camera Live ID</th>
                  <th>Location</th>
                  <th>Distance (km)</th>
                </tr>
              </thead>
              <tbody>
                 {resultsJSX}   
              </tbody>
            </table>
          </div>
        </div>

      </div>
    )
  }
}

export default CameraSearchForm