import alt from '../alt-application'

class CameraSearchFormActions{
  constructor(){
    this.generateActions('updateResults', 'resetResults')
  }

  getNN(data){
    this.getClosestCameras(data).then((res)=>{
      this.updateResults(res)
    });
  }

  getClosestCameras(data){
    return new Promise((resolve, reject) => {
      $.post('http://water.ccny.cuny.edu/lsxliron/findNN', JSON.stringify(data), (results) =>{
        if (results.nn)
          resolve(results.nn)
        else
          reject(-1)
      })
    })
  }

  clearResults(){
    return this.resetResults()
  }
}
export default alt.createActions(CameraSearchFormActions)