import alt from '../alt-application'

class GMapActions{
  constructor(){
    this.generateActions('initMarkers', 'resetMarkers');
  }

  setupMarkers(map){
    this.getAllcams().then((results) => {
      return this.initMarkers({cams: results, map:map})
    });

    return true;
  }

  getAllcams(){
    return new Promise((resolve, reject) => {
      $.get("http://water.ccny.cuny.edu/lsxliron/getAllCams", function(data){
        if (data.cams){
          resolve(data.cams)
        }
        else
          reject(-1)
      })
    });
  }

  showAllCams(){
    return this.resetMarkers()
  }

}

export default alt.createActions(GMapActions)