import React from 'react'
import DaysHandlerStore from '../stores/DaysHandlerStore'
import DaysHandlerActions from '../actions/DaysHandlerActions'

class DaysHandler extends React.Component{
  constructor(props){
    super(props)
    this.state = DaysHandlerStore.getState()
    this.onChange = this.onChange.bind(this)
    this.selectDay = this.selectDay.bind(this)
  }

  onChange(){
    this.setState(DaysHandlerStore.getState())
  }

  componentDidMount(){
    DaysHandlerStore.listen(this.onChange)
  }

  componentWillUnmount(){
    DaysHandlerStore.unlisten(this.onChange)
  }

  selectDay(event){
    var clickedBtn = event.target.id
    var color;
    DaysHandlerActions.updateDaySelection(clickedBtn)
    this.state.days[clickedBtn]?color="#5bc0de":color="white"
    $(`#${clickedBtn}`).css('background-color',color)
  }

  
  render(){
    var days=['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat']
    var daysJSX = days.map((d)=>{
      return (<button id={d} key={d} type="button" style={{backgroundColor:this.state.days[d]?"#5bc0de":"white", opacity:"0.5"}} onClick={this.selectDay} className="btn btn-default">{d}</button>)
    })

    return(
      <div>
        <div className="btn-group" role="group">
          {daysJSX}
        </div>
      </div>
    )
  }
}
export default DaysHandler