import React from 'react';
import {Link} from 'react-router';

class MapResultTableRow extends React.Component{
  constructor(props){
    super(props)
  }

  render(){
    return (
      <tr>
        <td><a href={`http://dotsignals.org/multiview2.php?listcam=${this.props.live_id}`} target="_blank">{this.props.live_id}</a></td>
        <td>{this.props.location}</td>
        <td>{this.props.distance}</td>
        <td>
          <Link to={`/search/${this.props.live_id}`}>
            <button className="btn btn-info" onClick={this.passCamIdToForm}>Search</button>
          </Link>
        </td>
      
      </tr>
    );
  }
}


export default MapResultTableRow