import alt from '../alt-application'
import GMapActions from '../actions/GMapActions'
import CameraSearchFormStore from './CameraSearchFormStore';

class GMapStore{
  constructor(){
    this.bindActions(GMapActions);
    this.markers = [];
    this.cams = []
    this.gMap = null
  }

  onInitMarkers(payload){
    let cams = payload.cams;
    let map = payload.map;
    this.cams = cams
    cams.map((d) => {
      let marker = this.makeMarker(d.lat, d.lon, map)
      let content = `<strong>${d.location}</strong><br />ID: ${d.live_id}`
      let info = new google.maps.InfoWindow({content, content})
      marker.addListener('click', function(){info.open(map, marker)})
      this.markers.push(marker)
    });
  }

  makeMarker(lat, lng, map){
    return new google.maps.Marker({
          map: map,
          animation: google.maps.Animation.DROP,
          position: {lat: lat, lng: lng}
        });
  }

  onResetMarkers(){
    this.markers.map((m)=>{
      return m.setVisible(true)
    })
  }

}

export default alt.createStore(GMapStore)