import alt from '../alt-application'
import DaysHandlerActions from '../actions/DaysHandlerActions'

class DaysHandlerStore{
  constructor(){
    this.bindActions(DaysHandlerActions)
    this.days = {"Sun":true, "Mon":true, "Tue":true, "Wed":true, "Thu":true,
                 "Fri":true, "Sat":true}
  }

  onUpdateSelectedDays(day){
    this.days[day] = (!this.days[day])
    return true
  }

  onReset(){
    console.log("RRR")
    this.days = {"Sun":true, "Mon":true, "Tue":true, "Wed":true, "Thu":true,
                 "Fri":true, "Sat":true}
    return true
  }

}

export default alt.createStore(DaysHandlerStore)